.divServicios{
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    display: flex;
    z-index: 1;

}
.divFondoServicios{
    background-image: url(../../assests/img/pictures/serviciosClaro.jpg);
    background-size: cover;
    background-position: center;
    width: 100vw;
    height: 100vh;
}


.divServicios h1{
    font-size: 2.8rem;
    font-weight: 400;
    margin-top: 2rem;
    text-align: start;
    padding: 2rem;

}

.divInfoServicios{
    display: flex;
    flex-direction: column;
    width: 100%;

    color:white;
    gap: 1rem;
    padding: 2rem;

}


.divWeb,
.divConcept{
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    padding: 1rem 0;
}

.divLanguages{

    width: 100%;
    position: absolute;
    bottom: 15px;

}


.divLanguages img{
    width: 100%;
    height: auto;
    padding-right:5rem;
    padding-left: 1rem;

}

.divConWeb{
    display: flex;
    gap: 2.5rem;
    text-align: start;
    font-weight:400;
}


.divConWeb h3 {

    font-weight:300;
}

.divTitleServicios{
    background-color: #6d6d6d;
    height: 100vh;
    position: relative;
}

@media (min-width: 1200px){

    .divServicios h1{
        font-size: 4rem;
    }
    .divConWeb h2{
        font-size: xx-large;
    }
    .divConWeb h3 {
       font-size: x-large;
    }
}