.divCarousel{
    position: relative;
    height: 100%;
}
.swiper {
    width: 40%
  }
  
  .swiper-slide {
    background-position: center;
    background-size: cover;
  }
  
  .swiper-slide img {
    display: block;
    width: 100%;
  }

.imgSlider{
    width: 100%;
    height: auto;
    object-fit: cover !important;

}

.swiper-wrapper{
    height: auto;
}
.swiper-cube .swiper-cube-shadow{
    display: none;
}

.swiper-pagination{
    bottom: -8% !important;
}
.divTitleBtnSlider{
    display: flex;
    flex-direction: column;
    gap: 0.8rem;
}
.divTitleBtnSlider h3{
    font-size: 1.5rem;
    font-weight: 300;
}

.divTitleBtnSlider button {
    font-size: large;
    padding: 0.6rem;
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    background-color:rgba(0,0,0,0.35);
    border: 3px solid white;
    color: white;
    width:fit-content;
    margin: 0  auto;
}
.divTitleBtnSlider button:hover{

    border: 3px solid black;
    background-color: rgba(255, 255, 255, 0.327);
    font-weight: 500;  
    cursor: pointer;    
}


.swiper-button-prev, .swiper-rtl .swiper-button-next {
    left: -50px !important;
    right: auto !important;
    color: white !important;
}
.swiper-button-next, .swiper-rtl .swiper-button-prev {
    right: -50px !important;
    left: auto !important;
    color: white !important;
}

@media (min-width: 1200px){
    .swiper {
        width: 43%
      }
       
}
@media (min-width: 1400px){
    .swiper {
        width: 41%
    }
       
}